.library{
    position:fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background-color: white;
    box-shadow: 2px 2px 50px gray;
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    // opacity: 0;
    h2{
        padding: 2rem;
    }
    h2{
        padding: 2rem;
    }
    .library-song{
        align-items: center;
        display: flex;
        padding: 1rem 2rem 1rem 2rem;
        cursor: pointer;
        img{
            width: 50%;
        }
        &:hover{
background-color: rgb(230, 173, 230);
        }
    
    }
    
}


 .song-description{
    padding-left: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7rem;
    }
 }

 *{
    scrollbar-width: thin;
    scrollbar-color: rgba(155,155,155, 0.5) transparent;
 }

 *::-webkit-scrollbar{
    width: 5px;
 }
 *::-webkit-scrollbar-thumb{
    background-color:rgba(155,155,155, 0.5) ;
    border-radius: 20px;
    border: transparent;
 }
 *::-webkit-scrollbar-track{
   background: transparent ;
 }

 .selected{
    background-color: pink;
 }
.active-library{
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width: 768px) {
    .library{
        width: 100%;
    }
}